<template>
  <v-container>
    <v-row>
      <v-col cols="12" sm="6" md="4">
        <span class="body-1 font-weight-medium"> Coupes </span>
        <v-chip
          v-if="worldCups?.length"
          color="purple lighten-1 white--text"
          small
        >
          {{ worldCups?.length }} éditions
        </v-chip>
      </v-col>
    </v-row>
    <v-row no-gutters>
      <v-col cols="12" sm="7" md="6" lg="5">
        <v-chip-group
          active-class="red--text"
          column
          mandatory
          v-model="chipGroup"
        >
          <v-chip filter outlined v-for="tag in tags" :key="tag.tag">
            <v-icon left>
              {{ tag.icon }}
            </v-icon>
            {{ tag.tag }}
          </v-chip>
        </v-chip-group>
      </v-col>
    </v-row>

    <v-row no-gutters v-if="getChipValue == 0">
      <v-col cols="6" sm="6" md="4">
        <v-text-field
          color="red"
          v-model="yearFilter"
          label="Filtrer par année"
        />
      </v-col>
    </v-row>

    <v-row v-if="getChipValue == 0">
      <v-col
        v-for="worldCup in filteredWorldCups"
        :key="worldCup.year"
        cols="12"
        sm="6"
        md="4"
      >
        <v-card style="background-color: #f5f5f5">
          <v-card-title>Coupe du monde {{ worldCup.year }}</v-card-title>
          <v-card-text>
            <p>
              <v-icon>mdi-flag</v-icon> Nombre de participants:
              <v-chip>{{ worldCup.teams }}</v-chip>
            </p>
            <p>
              <v-icon>mdi-soccer-field</v-icon> Nombre de matchs:
              <v-chip>{{ worldCup.matches }}</v-chip>
            </p>
            <p>
              <v-icon color="red darken-1">mdi-podium-silver</v-icon>
              Finaliste:
              {{ worldCup.runner_up }}
            </p>
            <p>
              <v-icon color="yellow darken-1">mdi-trophy</v-icon> Champion:
              {{ worldCup.champion }}
            </p>
            <p>
              <v-icon color="accent darken-1">mdi-soccer</v-icon> Meilleur
              buteur : {{ worldCup.top_scorrer.split("-")[0] }}
              <v-chip>{{ worldCup.top_scorrer.split("-")[1] }}</v-chip>
            </p>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>

    <v-row v-if="loading == true">
      <v-progress-linear indeterminate></v-progress-linear>
    </v-row>

    <v-row v-if="getChipValue == 1" class="justify-center align-center">
      <v-container>
        <template>
          <v-timeline>
            <v-timeline-item
              v-for="(cup, i) in worldCups"
              :key="i"
              :color="colors[i]"
              icon="mdi-trophy"
              fill-dot
            >
              <template v-slot:opposite>
                <span
                  class="headline font-weight-bold"
                  :style="`color: ${colors[i]}`"
                  >{{ cup.year }}</span
                >
              </template>
              <div class="py-4">
                <h2
                  class="headline font-weight-light mb-4"
                  :style="`color: ${colors[i]}`"
                >
                  {{ cup.champion }}
                </h2>
                <div>
                  Equipe Finaliste : {{ cup.runner_up }}
                  <v-icon color="red">mdi-podium-silver</v-icon>
                  - Attendance :
                  {{ cup.attendance }}
                  <v-icon>mdi-eye</v-icon>
                  - Meilleur buteur : {{ cup.top_scorrer }} buts
                  <v-icon color="light-blue">mdi-soccer</v-icon>
                  - Nombre de matchs: {{ cup.matches }}
                  <v-icon>mdi-soccer-field</v-icon>
                  - Pays Hôte :
                  {{ cup.hostedByHosts[0].name }}
                  <v-icon>mdi-flag</v-icon>
                </div>
              </div>
            </v-timeline-item>
          </v-timeline>
        </template>
      </v-container>
    </v-row>
  </v-container>
</template>

<script>
import { GET_CUPS } from "@/graphql/queries";

export default {
  name: "CoupeView",
  data: () => ({
    yearFilter: "",
    worldCups: [],
    loading: false,
    chipGroup: "",
    tags: [
      { tag: "Filtrer par année", icon: "mdi-calendar" },
      {
        tag: "Afficher la timelines des champions",
        icon: "mdi-timeline-clock",
      },
    ],
    colors: [],
  }),
  methods: {
    async getCups() {
      this.loading = true;
      await this.$apollo
        .query({
          query: GET_CUPS,
        })
        .then((result) => {
          this.worldCups = result.data.cups;
          this.loading = false;
        });
    },
    getColor() {
      return (
        "#" +
        Math.floor(Math.random() * 16777215)
          .toString(16)
          .padStart(6, "0")
          .toUpperCase()
      );
    },
    generateColors() {
      for (let index = 0; index < this.worldCups.length; index++) {
        this.colors.push(this.getColor());
      }
    },
  },
  computed: {
    filteredWorldCups() {
      return this.worldCups?.filter((worldCup) => {
        return worldCup.year.toString().includes(this.yearFilter);
      });
    },
    getChipValue() {
      return this.chipGroup;
    },
  },
  created() {
    this.$watch(
      "$apollo.loading",
      (loading) => {
        this.loading = loading;
      },
      { immediate: true }
    );
  },
  mounted() {
    this.getCups().then(() => this.generateColors());
  },
};
</script>
